body {
    background-color: #FFF5F6
}
.footer__title {
    @extend %visually-hidden;
}
.footer__content {    
    width: 100%;
    border-bottom: 1px solid #E1E1E1;
    @include respond-to('medium') {
            display: flex;
            justify-content:space-between;
            }
}
.footer__logo {
    margin-bottom: 40px;
    @include respond-to('medium') {
            display: flex;
            flex-direction: column;
            justify-content:flex-start;
            margin-bottom: 60px;
            }
}
.footer__connect {    
    margin-bottom: 40px;
    @include respond-to('medium') {
            display: flex;
            flex-direction: column;
            justify-content:center;
            margin-bottom: 60px;
        }
}

.footer__logo-top {
    font-family: 'Titan One', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 1.17;
    text-transform: uppercase;
    color: #FFA5BA;
    margin-bottom: 16px;
    max-width: 232px;
    @include respond-to('medium') {
        font-size: 26px;
        line-height: 1.15;
        letter-spacing: 0.04em;
        max-width: 340px;
        }
    @include respond-to('large') {
        font-size: 30px;
        line-height: 1.13;
        max-width: 474px;}
    }
.footer__logo-bottom {
    font-family: 'Titan One', sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 34px;
    line-height: 1.15;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #D41443;
    
    max-width: 200px;
    @include respond-to('medium') {
        font-size: 48px;
        line-height: 1.15;
        letter-spacing: 0.06em;
        max-width: 340px;
        }
    @include respond-to('large') {
        font-size: 58px;
        line-height: 1.13793;
        max-width: 500px;
    }
}


.socials__list {
    display: flex;
    justify-content: flex-end;
    gap: 28.24px;
    margin-bottom: 40px;
    @include respond-to('medium') {
        gap: 30px;
    }
}

.socials__item {
    width: 32px;
    height: 32px;    
}

.socials__link {
    text-decoration: none;
    width: 100%;
    height: 100%;
    background-color: #FEA5BB;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    transition-duration: 250ms;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);

    &:hover,
    &:focus {
        background-color: #D41443;
    }
}

.socials__icon {
    fill: #FFFFFF;
}
.footer__contact {
    display: flex;
    justify-content: flex-end;}

.footer__text {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.86;
    text-align: right;
    color: #000000;
}
.footer__tel {
    color: #D41443;
    text-decoration: none;
}

.footer__copyright {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;
    text-align: center;    
    color: #907E82;
    margin: 16px 0 3px 0;
    @include respond-to('medium') {
            margin: 8px 0;
        }    
        @include respond-to('large') {
            margin: 7px 0 4px 0;
        }
}
.footer__copytext {
    display: block;    
    @include respond-to('medium') {
        display: inline;     
    }}